.transformerTitle {
  margin-top: 80px;
  margin-bottom: 60px;
  text-align: center;
}

.translation {
  margin-top: 40px;
  text-align: center;
}

.taTitle {
  margin-top: 15px;
}

footer {
  margin-top: 50px;
}

.imageDiv {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  z-index: -100;

  img {
    position: absolute;
    left: 50%;
    top: 35%;
  }
}

@media (min-width: 992px) {
  .imageDiv {
    img {
      left: 60%;
    }
  }
}

@media (min-width: 1200px) {
  .imageDiv {
    img {
      left: 70%;
    }
  }
}
